<template>
  <div class="pb-5">

    <template v-for="(child,subindex) in comment.children">

      <!--ответ на комментарий-->
      <v-card
          elevation="0"
          :key="child.id + '_children'"
          :style="{width: (100 - 5)  + '%', marginLeft: 'auto' }"
          v-if="!child.isDeleted && !isEdit[child.id]"
      >
        <v-list-item three-line class="pr-0">
          <v-list-item-avatar
              left
              color="#C4C4C4"
              size="36"
          >
            AA
          </v-list-item-avatar>
          <v-list-item-content>

            <div class="text-subtitle-2 text-left mb-1">
              <span :style="getRandomColor(child.id)">{{ child.id }}</span>
              <span> {{ formatDate(child.dateCreated) }}</span>
              <span class="ml-2 change__comment">{{
                  new Date(child.dateModified) > new Date(child.dateCreated) ? "Изменено" : ""
                }}</span>
            </div>

            <template>
              <div class="text-justify mb-0 comment-text">
                <strong>@{{ child.id }}</strong> {{ child.commentText }}
              </div>
            </template>
          </v-list-item-content>

        </v-list-item>

        <v-card-actions class="pa-0 ml-15 flex-wrap">
          <v-btn
              text
              :x-small="width <= 768"
              color="#000000"
              class="text-capitalize text-body-1 font-weight-bold"
              @click.stop="showAnswerInput(child.id, child.path)"
          >
            {{ isAnswer[child.id] ? "Отменить" : "Ответить" }}
          </v-btn>

          <v-btn
              text
              :x-small="width <= 768"
              :disabled="isAnswer[child.id]"
              color="rgba(0,0,0,.7)"
              class="text-capitalize text-body-1 ml-0"
              @click.stop="showEditInput(child.id,child.path,child.commentText)"
          >
            {{ isEdit[child.id] ? "Отменить" : "Изменить" }}
          </v-btn>


          <v-btn
              text
              :x-small="width <= 768"
              color="rgba(0,0,0,.7)"
              class="text-capitalize text-body-1 ml-0"
              @click.stop="openConfirm(child.id)"
          >
            Удалить
          </v-btn>

        </v-card-actions>

      </v-card>
      <!--Удаленный коммент-->
      <v-card
          :key="child.id + '_del'"
          elevation="0"
          v-else-if="child.isDeleted && !isEdit[child.id]"
          :style="{width: (100 - 5)  + '%', marginLeft: 'auto' }"
      >
        <v-list-item three-line class="pr-0">
          <v-list-item-avatar
              left
              color="#C4C4C4"
              size="36"
          >
            AA
          </v-list-item-avatar>
          <v-list-item-content>

            <div class="text-subtitle-2 text-left mb-1">
              <span :style="getRandomColor(child.id)">{{ child.id }}</span>
            </div>

            <template>
              <div class="text-justify mb-0 comment-text__deleted">
                Комментарий удален &nbsp;{{ formatDeleteDate(child.dateModified) }}
              </div>
            </template>
          </v-list-item-content>

        </v-list-item>

      </v-card>
      <!--форма ответа на комментарий-->
      <v-card
          :style="{width: (100 -  5)  + '%', marginLeft: 'auto' }"
          :key="child.id + '_answer'"
          class="d-flex flex-column"
          elevation="0"
          v-if="isAnswer[child.id]"
      >
        <v-list-item
            class="pr-0"
            dense
        >
          <v-list-item-avatar
              class="mb-0"
              left
              color="#C4C4C4"
              size="36"
          >
            AA
          </v-list-item-avatar>
          <v-card-actions class="textarea__btns pa-0" v-if="isAnswerFocus[child.id]">

            <v-btn-toggle
                color="primary"
                dense
                group
                multiple
            >
              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small

              >
                <v-icon>
                  mdi-format-bold
                </v-icon>
              </v-btn>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  mdi-format-italic
                </v-icon>
              </v-btn>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  more_horiz
                </v-icon>
              </v-btn>

              <span class="vertical-border"></span>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  mdi-format-list-bulleted
                </v-icon>
              </v-btn>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  mdi-format-list-numbered
                </v-icon>
              </v-btn>

              <span class="vertical-border"></span>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  format_color_text
                </v-icon>
              </v-btn>

              <span class="vertical-border"></span>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  mdi-link
                </v-icon>
              </v-btn>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  mdi-image
                </v-icon>
              </v-btn>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  alternate_email
                </v-icon>
              </v-btn>

              <v-btn
                  class="ma-0"
                  color="black"
                  ripple
                  text
                  x-small
              >
                <v-icon>
                  sentiment_satisfied_alt
                </v-icon>
              </v-btn>


            </v-btn-toggle>
          </v-card-actions>
          <v-textarea
              class="mt-2"
              rows="1"
              solo
              placeholder="Добавить комментарий..."
              outlined
              rounded
              hide-details
              no-resize
              dense
              :auto-grow="isAnswerFocus[child.id]"
              wrap="hard"
              maxlength="3000"
              v-model.trim="answerCommentObj.comment_text"
              @focus="showAnswerInputBtns(child.id)"
          >

          </v-textarea>
        </v-list-item>

        <v-row class="ma-0 mt-3 align-center justify-space-between">

          <div class="ml-15"></div>

          <v-btn
              class="black--text text-capitalize text-body-1 font-weight-medium"
              style="z-index: 1;"
              elevation="4"
              color="#E8D3E8"
              rounded
              font-size="1.2em"
              @click.stop="sendAnswerComment"
          >
            Отправить
          </v-btn>
        </v-row>


      </v-card>
      <!--Форма редактирования коммента-->
      <v-card
          class="d-flex flex-column"
          elevation="0"
          :key="'edit_comment_' + child.id"
          v-if="isEdit[child.id]"
          :style="{width: (100 - 5)  + '%', marginLeft: 'auto' }"
      >
        <v-list-item
            class="pr-0"
            dense
        >
          <v-list-item-avatar
              class="mb-0"
              left
              color="#C4C4C4"
              size="36"
          >
            AA
          </v-list-item-avatar>
          <v-textarea
              class="mt-2"
              rows="1"
              solo
              placeholder="Добавить комментарий..."
              outlined
              rounded
              hide-details
              no-resize
              dense
              auto-grow
              wrap="hard"
              maxlength="3000"
              v-model.trim="editCommentObj.comment_text"
          >

          </v-textarea>
        </v-list-item>

        <v-row class="ma-0 mt-3 align-center justify-space-between">

          <div class="ml-15">
            <v-btn
                v-if="isEdit[child.id]"
                text
                :x-small="width <= 768"
                color="#000000"
                class="text-capitalize text-body-1 font-weight-bold"
                @click.stop="isEdit[child.id] = !isEdit[child.id]"
            >
              Отменить
            </v-btn>
          </div>

          <v-btn
              class="black--text text-capitalize text-body-1 font-weight-medium"
              style="z-index: 1;"
              elevation="4"
              color="#E8D3E8"
              rounded
              font-size="1.2em"
              @click.stop="sendEditingComment"
          >
            Изменить
          </v-btn>
        </v-row>


      </v-card>
      <!--рекурсивный компонент-->
      <AnswerComment
          :key="subindex + '_' + child.id"
          :index="subindex"
          :comment="child"
          :parentItem="comment"

      />
      <!--Модальное окно-->
      <ConfirmModal :key="child.id" :dialog="isConfirm" @deleteComment=delComment @closeConfirm="isConfirm=false"/>

    </template>
  </div>

</template>

<script>
import {mapActions} from "vuex";
import ConfirmModal from "@/components/ConfirmModal";

export default {
  name: "AnswerComment",
  components: {ConfirmModal},
  props: {
    comment: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    parentItem: {
      required: false,
    },
  },
  data() {
    return {
      colors: ["#0038FF", "#FE0000", "#28A015"],
      width: 0,
      isAnswer: {},
      isAnswerFocus: {},
      isEdit: {},
      isConfirm: false,
      answerCommentObj: {
        path: "",
        page_id: "1",
        comment_text: "",
        is_private: true,
        attachment: [],
        user_id: "78",
      },
      editCommentObj: {
        id: null,
        comment_text: "",
        is_private: true,
      },
      answerPath: "",
    };
  },
  methods: {
    ...mapActions([
      "SET_COMMENT",
      "DEL_COMMENT",
      "EDIT_COMMENT"
    ]),

    /**
     * Метод форматирования даты удаления
     * @param date
     * @returns {string}
     */
    formatDeleteDate(date) {
      date += "Z";
      let oldDate = new Date(date);
      let options = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
      };
      return oldDate.toLocaleString("ru-RU", options);
    },

    /**
     * Метод открытия модального окна подтверждения удаления
     * @param id
     */
    openConfirm(id) {
      this.commentId = id;
      this.isConfirm = true;
    },

    /**
     * Метод отправки отредактированного комментария
     * @returns {Promise<void>}
     */
    async sendEditingComment() {
      if (this.editCommentObj.comment_text !== "") {

        this.$set(this.editCommentObj, "id", this.answerId);

        await this.EDIT_COMMENT(this.editCommentObj);
        this.editCommentObj = {
          id: null,
          comment_text: "",
          is_private: true,
        };
        for (let itemId in this.isEdit) {
          this.isEdit[itemId] = false;
        }
      }
    },

    /**
     * Метод удаления комментария
     * @returns {Promise<void>}
     */
    async delComment() {
      await this.DEL_COMMENT(this.commentId);
      this.isConfirm = false;
    },

    /**
     * Метод установления рандомного цвета для имени пользователя
     * @returns {{color: string}}
     */
    getRandomColor() {
      return {
        color: this.colors[Math.floor(Math.random() * this.colors.length)],
      };
    },

    /**
     * Метод форматирования даты
     * @param date
     * @returns {string}
     */
    formatDate(date) {
      date += "Z";
      let oldDate = new Date(date);
      let options = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
      };
      return oldDate.toLocaleString("ru-RU", options);
    },

    /**
     * Метод установки рандомного цвета текста для имени пользователя
     */
    handleResize() {
      this.width = window.innerWidth;
    },

    /**
     * Метод отображения инпута для ответа
     * @param id
     */
    showAnswerInput(id, path) {

      this.answerPath = path;
      this.answerId = id;
      if (!this.isAnswer[id]) {
        for (let itemId in this.isAnswer) {
          this.isAnswer[itemId] = false;
          this.isAnswerFocus[itemId] = false;
        }
        this.$set(this.isAnswer, id, false);
      }
      this.isAnswer[id] = !this.isAnswer[id];

    },

    async sendAnswerComment() {
      if (this.answerCommentObj.comment_text !== "") {
        this.$set(this.answerCommentObj, "parent_path", this.answerPath);
        await this.SET_COMMENT(this.answerCommentObj);

        this.answerCommentObj = {
          parent_path: "",
          page_id: "1",
          comment_text: "",
          is_private: true,
          attachment: [],
          user_id: "78",
        };
        for (let itemId in this.isAnswer) {
          this.isAnswer[itemId] = false;
        }
      }
    },

    /**
     * Метод отображения блока с кнопками для редактирования текста
     * @param id
     */
    showAnswerInputBtns(id) {
      let textarea = document.querySelector("textarea");
      textarea.id = id;
      if (!this.isAnswerFocus[id]) {
        for (let itemId in this.isAnswerFocus) {
          this.isAnswerFocus[itemId] = false;
        }
        this.$set(this.isAnswerFocus, id, false);
      }
      this.isAnswerFocus[id] = !this.isAnswerFocus[id];
      document.getElementById(id).style.paddingTop = "50px";
    },

    /**
     * Метод отображения инпута для редактирования
     * @param id
     */
    showEditInput(id, path, text) {
      this.answerPath = path;
      this.answerId = id;
      this.editCommentObj.comment_text = text;
      if (!this.isEdit[id]) {
        for (let itemId in this.isEdit) {
          this.isEdit[itemId] = false;
        }
        this.$set(this.isEdit, id, false);
      }
      this.isEdit[id] = !this.isEdit[id];
    },
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>

